import React, { useState } from "react";
import {
  Grid,
  Typography,
  TextField,
  Button,
  Link,
  IconButton,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import ArrowForwardRoundedIcon from "@mui/icons-material/ArrowForwardRounded";
import HeaderWrapper from "../../components/Wrapper/HeaderWrapper";
import { useFormik } from "formik";
import * as yup from "yup";
import { ReactComponent as GoogleIcon } from "assets/GoogleIcon.svg";
import { useNavigate } from "react-router";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

const useStyles = makeStyles((theme) => ({
  dmLoginRoot: {
    padding: 15,
    height: "80%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  dmLoginContentContainer: {
    width: "100%",
    "&>h4": {
      fontWeight: 600,
    },
    "&>p": {
      color: theme.palette.grey[500],
      marginTop: 5,
    },
  },
  dmLoginForm: {
    marginTop: 10,
    "&>div": {
      marginBottom: 10,
    },
    "&>button": {
      justifyContent: "space-between",
    },
  },
  dmOrPartition: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    margin: "10px 0px",
    "&:before, &:after": {
      content: '""',
      display: "block",
      width: "100%",
      height: 1,
      backgroundColor: theme.palette.grey[300],
      margin: "0px 3px",
    },
  },
  dmSignUpWithGoogleButton: {
    justifyContent: "flex-start !important",
    background: `${theme.palette.grey[100]} !important`,
    "&>svg": {
      marginRight: 15,
    },
  },
  dmForgotPassword: {
    marginTop: "3px !important",
    textAlign: "right",
  },
}));

const loginSchema = yup.object().shape({
  email: yup.string().email("Invalid email").required("Required"),
  password: yup
    .string()
    .required("Required")
    .min(8, "Between 8-16 Character")
    .max(16, "Between 8-16 Character"),
});

function Login() {
  const classes = useStyles();
  const navigate = useNavigate();

  const handleSignIn = (values, actions) => {
    // TODO: commented
    // try {
    //   const authentication = getAuth();
    //   let email = values.email;
    //   let password = values.password;
    //   signInWithEmailAndPassword(authentication, email, password)
    //     .then((response) => {
    //       console.log("response", response);
    //       console.log("response", response.user);
    //     })
    //     .catch((error) => {
    //       console.log(error.code);
    //       if (error.code === "auth/user-not-found") {
    //         setErrors({
    //           email: "User Not Found",
    //         });
    //       } else if (error.code === "auth/wrong-password") {
    //         setErrors({
    //           password: "Please check the Password",
    //         });
    //       } else {
    //         setErrors({
    //           email: "Fail to login",
    //         });
    //       }
    //     });
    // } catch (error) {}
  };

  const handleGoogleLogin = () => {
    // TODO: commented code
    // const auth = getAuth();
    // const provider = new GoogleAuthProvider();
    // provider.addScope("https://www.googleapis.com/auth/contacts.readonly");

    // signInWithPopup(auth, provider)
    //   .then((result) => {
    //     console.log("result", result);
    //     if (result.user) {
    //       navigate("/home");
    //     }
    //   })
    //   .catch((error) => {
    //     console.log("error", error);
    //   });
  };

  const {
    values,
    handleChange,
    handleBlur,
    errors,
    touched,
    setErrors,
    handleSubmit,
  } = useFormik({
    initialValues: { email: "", password: "" },
    onSubmit: handleSignIn,
    validationSchema: loginSchema,
  });

  const [showPassword, setShowPassword] = useState(false);

  const handleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  return (
    <HeaderWrapper>
      <Grid className={classes?.dmLoginRoot}>
        <Grid className={classes.dmLoginContentContainer}>
          <Typography variant="h4">Login</Typography>
          <Typography variant="body2">Please login to continue</Typography>
          <form className={classes.dmLoginForm} onSubmit={handleSubmit}>
            <TextField
              fullWidth
              name="email"
              placeholder="Email"
              label="Email"
              value={values.email}
              onChange={handleChange}
              onBlur={handleBlur}
              helperText={touched.email && errors.email}
              error={touched.email && Boolean(errors.email)}
            ></TextField>
            <TextField
              fullWidth
              type={showPassword ? "text" : "password"}
              name="password"
              placeholder="Password"
              label="Password"
              value={values.password}
              onChange={handleChange}
              onBlur={handleBlur}
              helperText={touched.password && errors.password}
              error={touched.password && Boolean(errors.password)}
              InputProps={{
                endAdornment: (
                  <IconButton
                    onClick={handleShowPassword}
                    onMouseDown={(e) => e.preventDefault()}
                    edge="end"
                  >
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                ),
              }}
            ></TextField>
            <Button
              fullWidth
              endIcon={<ArrowForwardRoundedIcon />}
              type="submit"
              size="large"
            >
              Login
            </Button>
          </form>
          <Typography variant="subtitle2" className={classes.dmForgotPassword}>
            Don't have account?&nbsp;
            <Link underline="hover" variant="subtitle2" href="/signup">
              Create One
            </Link>
          </Typography>
          <Grid className={classes.dmOrPartition}>
            <Typography>OR</Typography>
          </Grid>
          <Button
            fullWidth
            variant="contained"
            className={classes.dmSignUpWithGoogleButton}
            size="large"
            onClick={handleGoogleLogin}
          >
            <GoogleIcon width="35" height="35" />
            Continue with Google
          </Button>
        </Grid>
      </Grid>
    </HeaderWrapper>
  );
}

export default Login;
